//select2 (based on latest beta)
.selecttwo-select-wrapper {
  position: relative;

  .select2-container--default {
    //width: 100% !important;
    //max-width: 100% !important;
    width: 100% !important;
    max-width: 500px;

    .selection {
      .select2-selection__clear {
        /*width: 26px;
        height: 24px;
        line-height: 24px;
        font-size: 16px;
        font-weight: normal;*/

        /*min-height: 26px !important;
        height: 26px !important;
        width: 26px !important;

        margin-top: -1px;
        text-align: center !important;
        border: 1px solid var(--color-primary) !important;
        color: var(--color-primary) !important;
        border-radius: 50% !important;*/

        margin-right: 30px;
        margin-top: 4px;
        min-height: 20px !important;
        height: 20px !important;
        width: 20px !important;
        background-image: url("../icons/icon_close_ash.svg") !important;
        background-color: transparent !important;
        background-position: center center;
        background-size: cover;
        color: red;

        span{
          display: none;
        }
      }

      .select2-selection__arrow {
        width: 28px;
        display: inline-block;
      }
    }

    .select2-search--dropdown {
      .select2-search__field {
        min-height: auto !important;
        font-size: 12px !important;
        line-height: 1.2px !important;
      }
    }

    .select2-selection__rendered {
      margin: 0 !important;
      .select2-selection__choice {
        font-size: 14px !important;
        line-height: 22px !important;
        padding: 3px !important;
      }

      .select2-search {
        &.select2-search--inline {
          .select2-search__field {
            width: 100px !important;
            font-size: 12px !important;
            line-height: 22px !important;
            padding: 3px 5px !important;
            margin-top: 0 !important;
          }

        }
      }
    }

    .select2-selection--single{
      height: 30px;
    }
    .select2-selection--multiple {
      display: flex;
      flex-wrap: wrap;
      .select2-selection__rendered {
        display: flex;
        flex-wrap: wrap;

        .select2-selection__clear {
          //margin-left: auto !important;
          order: 99999;
          display: inline-block !important;
          margin: 2px 3px 2px 0 !important;
          //height: 26px;
          min-height: 26px;

          width: 26px;
          height: 24px;
          line-height: 24px;
          font-size: 16px;
          font-weight: normal;

          margin-top: 1px;
          text-align: center;
          border: 1px solid var(--color-primary);
          color: var(--color-primary);
          border-radius: 50%;
        }

        li {
          margin: 2px !important;
          display: inline-block !important;


          &.select2-selection__choice {
            background-color: transparent;
            color: var(--color-primary);

            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;

            .select2-selection__choice__remove {
              position: relative !important;
              background-image: url("../icons/icon_close_ash.svg") !important;
              background-position: center center;
              background-size: cover;
              //color: #fff !important;
              background-color: transparent !important;
              border: 0 none !important;
              //border: 1px solid var(--color-primary) !important;
              //border-radius: 50% !important;
              //position: relative;
              ///left: auto;
              //top: auto;

              font-size: 14px !important;
              line-height: 22px !important;
              width: 22px !important;
              height: 22px !important;
              min-height: 22px !important;
              max-height: 22px !important;
              display: inline-flex;
              margin-right: 5px !important;

              span{
                display: none;
                /*position: relative;
                top: 2px;
                left: -2px;
                width: 15px;
                height: 15px;
                display: inline-block;
                font-size: 14px;
                line-height: 1;*/
              }

              &:hover, &:focus {
                //background-color: transparent !important;
                background-color: rgba(93,93,255,.1882352941) !important;
                //color: #fff !important;
              }
            }
          }
        }
      }
      .select2-search.select2-search--inline{
        .select2-search__field{
          min-width: 150px;
          border: 1px solid var(--color-lightGrey);
          padding: 0 5px !important;
          &:focus{
            border-color: $app_primary_color;
          }
        }
      }
    }
  }
}
.select2-container--default.select2-container--open{
  .select2-results__option--highlighted[aria-selected]{
    background-color: $app_primary_color;
  }
}


