    }
  }
}

.cbx-listing-table-filters-wrap {
  margin-bottom: 10px;

  .col-12 {
    margin-bottom: 0;
  }

  .cbx-listing-table-filters {
    display: flex;
    flex-wrap: wrap;

    .cbx-listing-table-filter {
      margin-right: 10px;
      //max-width: 180px;
      //width: 100%;
      margin-bottom: 5px;

      input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
      select,
      textarea,
      textarea[type="text"] {

        width: 100% !important;
        display: inline-block !important;

        &.chota-inline {
          width: 100% !important;
          display: inline-block !important;
        }

        &.flatpickr-input-filter[type="text"] {
          width: auto !important;
        }

        &.select-filter[type="select"] {
          width: auto !important;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      &.cbx-listing-table-filter-autow {
        width: auto !important;
        max-width: max-content !important;
      }

      &.cbx-listing-table-filter-chooser {
        max-width: 100% !important;
        width: auto !important;
      }

      &.cbx-listing-table-filter-date-picker {
        display: flex;
        flex-wrap: wrap;
        max-width: max-content !important;

        //max-width: 100% !important;
        //width: auto !important;

        .flatpickr-input[type=text] {
          //width: 150px !important;
          //width: auto !important;
          //
        }

        select {
          width: auto !important;
          //flex-grow: 1;
          //width: auto !important;
        }

        //max-width: 250px;
      }


      &.pull-right {
        margin-left: auto !important;
        float: none !important;
      }

      &.pull-left {
        margin-right: auto !important;
        float: none !important;
      }
    }
  }
}

