//https://iconsvg.xyz/
//for button

.icon-inline {
  display: inline-flex !important;
  align-items: center !important;
}

.icon {
  display: inline-flex !important;
  align-items: center !important;

  .cbx-icon {
    margin-right: 5px;
    order: 1;
  }

  .button-label {
    order: 2;
  }

  &.icon-right {
    .cbx-icon {
      margin-right: 0 !important;
      margin-left: 5px !important;
      order: 2;
    }

    .button-label {
      order: 1;
    }
  }

  &.icon-only {
    .cbx-icon {
      margin-right: 0;
      margin-left: 0;
    }
  }

  &.icon-if-else {
    .cbx-icon:nth-child(2) {
      display: none;
    }
  }

  &.icon-else {
    .cbx-icon:nth-child(1) {
      display: none;
    }

    .cbx-icon:nth-child(2) {
      display: inline-flex !important;
    }
  }
}

//end for button

//for icons
.cbx-icon {
  background-size: contain !important;
  background-repeat: no-repeat !important;
  width: 20px;
  height: 20px;
  background-position: center;
  display: block;
  vertical-align: middle;
  background-color: transparent;
}

